
cc.Class({
    extends: cc.Component,

    properties: {

    },

    start () {

    },

    startTimer(time){
        // 设置每秒更新
        this.updateInterval = time;
        this.schedule(this.updateNumber, this.updateInterval);
    },

    stopTimer(){
        this.unschedule(this.updateNumber);
    },



    setLabel(string){
        this.node.getComponent(cc.Label).string = string
    },

    updateNumber(){
        // 生成随机数
        let randomNumber = Math.floor(Math.random() * 92) + 4;
                    
        // 更新 Label 显示的数字
        this.setLabel(randomNumber)
    },

    // update (dt) {},
});
