let httpDef = require('httpDef').httpDef;

let {Http, IHttpMethod } = require('Http');

let ConfirmPopup = require('ConfirmPopup');

let jieguoTimer = require('jieguoTimer');

let EventManager = require('EventManager');

let Decimal = require('decimal');


let Language = ["brasil","columbia"]

cc.Class({
    extends: cc.Component,

    properties: {
        language: {
            type: cc.Enum({
                Brasil: 0,
                Columbia: 1,
            }),
            default: 0
        },
        btn_dice: {
            type: cc.Button,
            default: null,
        },
        btn_music: {
            type: cc.Toggle,
            default: null,
        },
        txt_yuce: {
            type: cc.Label,
            default: null,
        },
        // txt_jieguo: {
        //     type: cc.Label,
        //     default: null,
        // },
        txt_Multiplicador: {
            type: cc.Label,
            default: null,
        },
        txt_Oportunidaddeganar: {
            type: cc.Label,
            default: null,
        },  
        toggle_scroll1: {
            type: cc.Toggle,
            default: null,
        }, 
        toggle_scroll2: {
            type: cc.Toggle,
            default: null,
        }, 
        slider : {
            type: cc.Slider,
            default: null,
        },
        progressBar : {
            type: cc.ProgressBar,
            default: null,
        },
        bar : {
            type: cc.Node,
            default: null,
        },
        progressLabel : {
            type: cc.Label,
            default: null,
        },
        btn_min: {
            type: cc.Button,
            default: null,
        },
        btn_max: {
            type: cc.Button,
            default: null,
        },
        btn_half: {
            type: cc.Button,
            default: null,
        },
        btn_double: {
            type: cc.Button,
            default: null,
        },
        txt_bet: {
            type: cc.Label,
            default: null,
        },  
        txt_score: {
            type: cc.Label,
            default: null,
        }, 
        btn_play: {
            type: cc.Button,
            default: null,
        },

        saizi_bg: {
            type: cc.Node,
            default: null,
        },
        mask: {
            type: cc.Node,
            default: null,
        },
        auto_bg2: {
            type: cc.Node,
            default: null,
        },
        
        manual_btn: {
            type: cc.Button,
            default: null,
        },
        auto_btn: {
            type: cc.Button,
            default: null,
        },

        editbox_number_of_bets: {
            type: cc.EditBox,
            default: null,
        },
        editbox_stop_on_profit: {
            type: cc.EditBox,
            default: null,
        },
        editbox_stop_on_loss: {
            type: cc.EditBox,
            default: null,
        },
        editbox_max_bet_amount: {
            type: cc.EditBox,
            default: null,
        },
        editbox_on_win: {
            type: cc.EditBox,
            default: null,
        },
        editbox_on_loss: {
            type: cc.EditBox,
            default: null,
        },
        toggle_on_win: {
            type: cc.Toggle,
            default: null,
        },
        toggle_on_loss: {
            type: cc.Toggle,
            default: null,
        },
        
        autoOk_btn: {
            type: cc.Button,
            default: null,
        },

        popupNode : {
            type: cc.Node,
            default: null,
        },

        
        txt_count: {
            type: cc.Label,
            default: null,
        },
        
        jieguoTimer:jieguoTimer,

        jiesuanLayer: {
            type: cc.Node,
            default: null,
        },

        tz_ani: {
            type: cc.Node,
            default: null,
        },

        tz: {
            type: cc.Sprite,
            default: null,
        },

        audioSource: cc.AudioSource,

        ruleLayer: {
            type: cc.Node,
            default: null,
        },
    },


    onLoad () {

        this.min_bet = 1
        this.max_bet = 128

        this.initData()
        

        //取Url参数
        let account = "";
        let token = "";
        var parmStr = window.location.href.split('?')[1];
        if (parmStr) {
            var parms = parmStr.split('&');
            parms.forEach(element => {
                var parm = element.split('=');
                if (parm[0] == "member_account") {
                    account = parm[1];
                }
                else if (parm[0] == "token") {
                    token = parm[1];
                }
            });
        }

        httpDef.ReqStartDefault.token = token
        httpDef.ReqStartDefault.language = "en"

        Http.request("https://game.yotuwin.com/dicegame/startDefault?token="+token,httpDef.ReqStartDefault,(data) => {
            cc.log(data);
            try {
                const parsedData = JSON.parse(data);
                this.gold = parsedData.data.gold
                // this.txt_score.string = parsedData.data.gold
            } catch (error) {
                console.error('解析 JSON 出错:', error);
            }
            
            
        },IHttpMethod.POST,false)

        cc.resources.preloadDir("game",function(){
            var infos = cc.resources.getDirWithPath('game', cc.SpriteFrame);
            let paths = infos.map(function (info) {
                return info.path;
            });
        }.bind(this));

        this.resList
        cc.resources.loadDir("game", function(err, assets){
 
            let list = cc.resources.getDirWithPath("game")
            let uuidDict = {}
         
            for(let item of list) {
                uuidDict[item.uuid] = item
            }
         
            let resList = {}
            
            for(let asset of assets) {
                let path = uuidDict[asset["_uuid"]].path
                if(!resList[path]) {
                    resList[path] = []
                }
                resList[path].push(asset)
            }
            this.resList = resList
         
        }.bind(this))

        this.btn_dice.node.on('click', this.ruleButtonClickEvent, this)
        this.btn_music.node.on('click', this.musicButtonClickEvent, this)
        
        this.toggle_scroll1.node.on('click', this.switchButtonClickEvent, this)
        this.toggle_scroll2.node.on('click', this.switchButtonClickEvent, this)
        this.slider.node.on('slide', this.sliderEvent, this);
        this.btn_play.node.on('click', this.playButtonClickEvent, this)

        this.manual_btn.node.on('click', this.switchAutoButtonClickEvent, this)
        this.auto_btn.node.on('click', this.switchAutoButtonClickEvent, this)
        this.autoOk_btn.node.on('click', this.switchAutoButtonClickEvent, this)
        

        
        this.btn_min.node.on('click', this.betButtonClickEvent, this)
        this.btn_max.node.on('click', this.betButtonClickEvent, this)
        this.btn_half.node.on('click', this.betButtonClickEvent, this)
        this.btn_double.node.on('click', this.betButtonClickEvent, this)

        this.editbox_number_of_bets.node.on('editing-did-ended', this.EditboxEvent, this);
        this.editbox_stop_on_profit.node.on('editing-did-ended', this.EditboxEvent, this);
        this.editbox_stop_on_loss.node.on('editing-did-ended', this.EditboxEvent, this);
        this.editbox_max_bet_amount.node.on('editing-did-ended', this.EditboxEvent, this);
        this.editbox_on_win.node.on('editing-did-ended', this.EditboxEvent, this);
        this.editbox_on_loss.node.on('editing-did-ended', this.EditboxEvent, this);
        
        this.toggle_on_win.node.on('click', this.resetIncreaseEvent, this);
        this.toggle_on_loss.node.on('click', this.resetIncreaseEvent, this);

        this.ConfirmPopup = this.popupNode.getComponent("ConfirmPopup")


    },

    initData(){

        this.bet = this.min_bet

        this.token = ""
        this.scrollSwitch = 0
        this.isAuto = false
        this.gold = 0       //分数
        this.balancegold = 0//输赢分数
        this.goldnumber = 0 //中奖数字
        this.prediction = 0 //预测数字
        this.multiplier = 0 //中奖倍率
        this.Oportunidaddeganar = 0//获奖几率
        this.play_btn_state = true

        this.numberofbet = 0
        this.stoponprofit = 1111111111
        this.stoponloss = 1111111111
        this.maxbetamount = 1111111111
        this.onwinincrease = 0
        this.onlossincrease = 0
        this.iswinresetIncrease = false
        this.islossresetIncrease = false
        this.totalwin = 0
        this.istop = true
    },

    resetData(){

        //this.bet = 100
        this.numberofbet = 0
        this.stoponprofit = 1111111111
        this.stoponloss = 1111111111
        this.maxbetamount = 1111111111
        this.onwinincrease = 0
        this.onlossincrease = 0
        this.iswinresetIncrease = false
        this.islossresetIncrease = false
        this.totalwin = 0
        this.toggle_on_win.isChecked = this.iswinresetIncrease 
        this.toggle_on_loss.isChecked = this.islossresetIncrease 
        this.isAuto = false
        this.istop = true
    },

    start () {
        this.toggle_on_win.isChecked = this.iswinresetIncrease 
        this.toggle_on_loss.isChecked = this.islossresetIncrease 
        //本地化
        EventManager.default.emit('lang-change', Language[this.language]);
    },

    update (dt) {

        //本地化
        EventManager.default.emit('lang-change', Language[this.language]);

        this.prediction = Math.round(this.slider.progress * 100)
        if (this.scrollSwitch === 0){
            this.multiplier = Help.preserveDecimal(97.5/Math.round(this.slider.progress * 100),2)
            this.Oportunidaddeganar = Math.round(this.slider.progress * 100)
        }
        else{
            this.multiplier = Help.preserveDecimal(97.5/(100 - Math.round(this.slider.progress * 100) - 1),2)
            this.Oportunidaddeganar = 100 - Math.round(this.slider.progress * 100) - 1
        }

        if (this.scrollSwitch === 0){
            if(Math.round(this.slider.progress * 100) < 1){
                this.slider.progress = 0.01
            }if(Math.round(this.slider.progress * 100) > 95){
                this.slider.progress = 0.95
            }
        }
        else{
            if(Math.round(this.slider.progress * 100) < 4){
                this.slider.progress = 0.04
            }if(Math.round(this.slider.progress * 100) > 98){
                this.slider.progress = 0.98
            }
        }

        this.txt_yuce.string = this.prediction 
        this.progressBar.progress = this.slider.progress
        this.progressLabel.string = this.prediction 

        this.txt_Multiplicador.string = this.multiplier
        this.txt_Oportunidaddeganar.string =  this.Oportunidaddeganar

        this.txt_score.string = this.gold
        // this.txt_jieguo.string = this.goldnumber
        // this.jieguoTimer.setLabel(this.goldnumber)

        this.btn_play.interactable = this.play_btn_state

        this.txt_bet.string = this.bet
        
        this.iswinresetIncrease = this.toggle_on_win.isChecked
        this.islossresetIncrease = this.toggle_on_loss.isChecked

        if (this.numberofbet > 0) {
            this.txt_count.string = this.numberofbet
            this.txt_count.node.active = true
        }else{
            this.txt_count.string = ""
            this.txt_count.node.active = false
        }
        


    

    },

    playAni(){
        this.tz.node.active = false
        this.tz_ani.active = true
        this.audioSource.play()
    },

    stopAni(){
        this.tz.node.active = true
        this.tz_ani.active = false
        let random = Math.floor(Math.random() * 6) + 1
        this.tz.spriteFrame = this.getRes("game/jz_" + random,cc.SpriteFrame)

        this.audioSource.pause();
    },

    //开始按钮
    playButtonClickEvent(){

        this.playAni()
        this.startGame()

    },

    startGame(){
        httpDef.ReqDiceGameBet.token = httpDef.ReqStartDefault.token
        httpDef.ReqDiceGameBet.language = "en"

        // this.balancegold = 10
        // this.bet = 1.05
        // this.onwinincrease = 10

        //上局赢了
        if (this.balancegold > 0){
            
            this.bet = Help.truncateToTwoDecimalPlacesAndHideZero(this.bet + (this.bet * ((this.iswinresetIncrease === true) ? this.onwinincrease : 0)/100),2)
            //this.bet = Number((this.bet + (this.bet * (( true) ? this.onwinincrease : 0)/100)).toFixed(2))
            // this.bet = Number((this.bet + (this.bet * ((this.iswinresetIncrease === true) ? this.onwinincrease : 0)/100)).toFixed(2))
        }else if(this.balancegold < 0){
            this.bet = Help.truncateToTwoDecimalPlacesAndHideZero(this.bet + (this.bet * ((this.islossresetIncrease === true) ? this.onlossincrease : 0)/100),2)
            // this.bet = Number((this.bet + (this.bet * ((this.islossresetIncrease === true) ? this.onlossincrease : 0)/100)).toFixed(2))
        }

        this.bet = (this.bet >= this.maxbetamount) ? this.maxbetamount : this.bet
        
        // let num = new Decimal(this.bet); 
        // this.bet = Number(Help.truncateToTwoDecimalPlaces(num))


        var num2 = this.bet.toFixed(3)
        this.bet = Number(num2.substring(0,num2.indexOf('.') + 3))

        // httpDef.ReqDiceGameBet.bet = this.bet
        httpDef.ReqDiceGameBet.bet = this.bet
        httpDef.ReqDiceGameBet.isblue = (this.scrollSwitch === 0) ? true : false
        httpDef.ReqDiceGameBet.prediction = this.prediction
        httpDef.ReqDiceGameBet.multiplier = this.multiplier
        httpDef.ReqDiceGameBet.isauto = this.isAuto
        httpDef.ReqDiceGameBet.numberofbet = this.numberofbet
        httpDef.ReqDiceGameBet.stoponprofit = this.stoponprofit
        httpDef.ReqDiceGameBet.stoponloss = this.stoponloss
        httpDef.ReqDiceGameBet.maxbetamount = (this.iswinresetIncrease === true || this.islossresetIncrease === true) ? this.maxbetamount : 99999999
        httpDef.ReqDiceGameBet.onwinincrease = (this.iswinresetIncrease === true) ? this.onwinincrease : 0
        httpDef.ReqDiceGameBet.onlossincrease = (this.islossresetIncrease === true) ? this.onlossincrease : 0
        httpDef.ReqDiceGameBet.totalwin = this.totalwin

        this.play_btn_state = false

        Http.request("https://game.yotuwin.com/dicegame/bet",httpDef.ReqDiceGameBet,function(data) {
            cc.log(data);
            try {

                this.jieguoTimer.startTimer(1/20)
                
                setTimeout(function(){
                    this.stopAni()
                    this.jieguoTimer.stopTimer()
                    const parsedData = JSON.parse(data);
                    if(parsedData.code === -1){
                        //this.dialog("",parsedData.msg)
                        this.dialog("","Por favor, saia da sala e entre novamente no jogo")
                        
                        return
                    }
                    this.gold = parsedData.data.gold
                    this.goldnumber = parsedData.data.goldnumber
                    this.jieguoTimer.setLabel(this.goldnumber)
                    
                    this.totalwin = parsedData.data.totalwin
                    
                    this.balancegold = parsedData.data.balancegold
                    this.istop = parsedData.data.istop
                    if (this.istop === true){
                        this.play_btn_state = true
                        this.manual_btn.isChecked = true
                        this.manual_btn.auto_btn = false
                        this.resetData()
                        this.editbox_number_of_bets.string = (this.numberofbet === 0) ? "" : this.numberofbet
                        this.editbox_stop_on_profit.string =  ""//(this.stoponprofit === 0) ? "" : this.stoponprofit
                        this.editbox_stop_on_loss.string =  ""//(this.stoponloss === 0) ? "" : this.stoponloss
                        this.editbox_max_bet_amount.string = ""//(this.maxbetamount === 0) ? "" : this.maxbetamount
                        this.editbox_on_win.string = (this.onwinincrease === 0) ? "" : this.onwinincrease
                        this.editbox_on_loss.string = (this.onlossincrease === 0) ? "" : this.onlossincrease
                    }

                    let jiesuanLayerOptions = {
                        num :this.balancegold,
                        parent:this
                    }
                    this.jiesuanLayer.getComponent("jiesuanLayer").show(jiesuanLayerOptions) 

                    if(this.isAuto === true){
                        this.numberofbet = parsedData.data.numberofbet

                        setTimeout(function(){
                            this.jiesuanLayer.getComponent("jiesuanLayer").hide()
                        }.bind(this),700)

                        setTimeout(function(){
                            this.playButtonClickEvent()
                        }.bind(this),1000)
                        
                    }else{
                        this.play_btn_state = true

                        setTimeout(function(){
                            this.jiesuanLayer.getComponent("jiesuanLayer").hide()
                        }.bind(this),1000)
                    }
                    

                }.bind(this), 2000)

            } catch (error) {
                console.error('解析 JSON 出错:', error);
            }
        }.bind(this),IHttpMethod.POST,false)
    },



    //规则按钮
    ruleButtonClickEvent(){
        this.ruleLayer.getComponent("ruleLayer").show({parent:this})  
    },

    //music按钮
    musicButtonClickEvent(event){
        
        this.audioSource.volume = event.isChecked

    },

    

    //选择蓝红按钮
    switchButtonClickEvent(event){
        if(event.node.name === "toggle1"){
            this.scrollSwitch = 0
            this.saizi_bg.getComponent(cc.Sprite).spriteFrame = this.getRes("game/saizi_bg1",cc.SpriteFrame)
            this.progressBar.node.getComponent(cc.Sprite).spriteFrame = this.getRes("game/slider3",cc.SpriteFrame)
            this.bar.getComponent(cc.Sprite).spriteFrame = this.getRes("game/slider2",cc.SpriteFrame)
        }else if (event.node.name === "toggle2"){
            this.scrollSwitch = 1
            this.saizi_bg.getComponent(cc.Sprite).spriteFrame = this.getRes("game/saizi_bg2",cc.SpriteFrame)
            this.progressBar.node.getComponent(cc.Sprite).spriteFrame = this.getRes("game/slider2",cc.SpriteFrame)
            this.bar.getComponent(cc.Sprite).spriteFrame = this.getRes("game/slider3",cc.SpriteFrame)
        }
    },

    //选择模式按钮
    switchAutoButtonClickEvent(event){
        if(event.node.name === "manual_btn"){
            this.resetData()

            this.editbox_number_of_bets.string = ""
            this.editbox_stop_on_profit.string = ""
            this.editbox_stop_on_loss.string = ""
            this.editbox_max_bet_amount.string = ""
            this.editbox_on_win.string = ""
            this.editbox_on_loss.string = ""
            this.isAuto = false
            this.auto_bg2.active = false
            this.mask.active = false
        }else if (event.node.name === "auto_btn"){
            this.isAuto = false
            this.auto_bg2.active = true
            this.mask.active = true
        }else if (event.node.name === "autoOk_btn"){

            if(this.numberofbet === 0){
                this.dialog("","número de apostas não pode estar vazio!")
                return
            }

            // if(this.stoponprofit === 0 || this.stoponloss === 0){
            //     this.dialog("","stop on profit or stoponloss cannot be empty!")
            //     return
            // }

            if(this.iswinresetIncrease === true || this.islossresetIncrease === true){
                if(Number(this.editbox_max_bet_amount.string) === 0 || this.editbox_max_bet_amount.string === ""){
                    this.dialog("","Quando o aumento na ganha ou o aumentar a perda está ativado, o valor máx de aposta não pode servazio!")
                    return
                }
                // if(this.maxbetamount === 0){
                //     this.dialog("","Quando o aumento na ganha ou o aumentar a perda está ativado, o valor máx de aposta não pode servazio!")
                //     return
                // }
            }

            this.balancegold = 0
            this.isAuto = true
            this.auto_bg2.active = false
            this.mask.active = false

            this.myUpdate = function () {
                
                this.playButtonClickEvent()

            };

            this.scheduleOnce(this.myUpdate,1);
    


        }
    },

    //下注按钮
    betButtonClickEvent(event){

        if(event.node.name === "btn_min"){
            this.bet = this.min_bet
        }else if (event.node.name === "btn_max"){
            this.bet = this.max_bet
        }else if (event.node.name === "btn_half"){
            this.bet = (this.bet/2 < this.min_bet) ? this.min_bet : this.bet/2
        }else if (event.node.name === "btn_double"){  
            this.bet = (this.bet*2 > this.max_bet) ? this.max_bet : this.bet*2
        }

    },

    //输入框
    EditboxEvent(event){
        
        if(event.node.name === "editbox_number_of_bets"){
            this.numberofbet = Number(event.string)
        }else if (event.node.name === "editbox_stop_on_profit"){
            this.stoponprofit = Number(event.string)
        }else if (event.node.name === "editbox_stop_on_loss"){
            this.stoponloss = Number(event.string)
        }else if (event.node.name === "editbox_max_bet_amount"){  
            if (Number(event.string)===0){return}
            this.maxbetamount = Number(event.string)
        }else if (event.node.name === "editbox_on_win"){ 
            if(Number(event.string) > 100){
                this.editbox_on_win.string = 0
            } 
            this.onwinincrease = Number(event.string)
        }else if (event.node.name === "editbox_on_loss"){  
            if(Number(event.string) > 100){
                this.editbox_on_loss.string = 0
            } 
            this.onlossincrease = Number(event.string)
        }

    },

    resetIncreaseEvent(event){

        //todo
       //onwin onloss按钮相关没做



        // if(event.node.name === "toggle_on_win"){
        //     this.iswinresetIncrease = event.isChecked
        // }else if (event.node.name === "toggle_on_loss"){
        //     this.islossresetIncrease = event.isChecked
        // }
        
    },

    //
    sliderEvent(){

        if (this.scrollSwitch === 0){
            if(Math.round(this.slider.progress * 100) < 1){
                this.slider.progress = 0.01
            }if(Math.round(this.slider.progress * 100) > 95){
                this.slider.progress = 0.95
            }
        }
        else{
            if(Math.round(this.slider.progress * 100) < 4){
                this.slider.progress = 0.04
            }if(Math.round(this.slider.progress * 100) > 98){
                this.slider.progress = 0.98
            }
        }


    },

    getRes(path, type) {
        let list = this.resList[path]
     
        if(!list) {
            return null
        }
     
        for(let item of list) {
            if(item instanceof(type)) {
                return item
            }
        }
     
        return null
    },

    dialog(title,content,confirmCallback){
        const confirmOptions = {
            title: title,
            content: content,
            confirmCallback: confirmCallback
        };
        
        this.ConfirmPopup.show(confirmOptions)
    },
});
